import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import { v4 as uuidv4 } from 'uuid'
import type { 
  WorkingHours, 
  DateOverride, 
  TimeBlock,
  DateOverrideType
} from '@/lib/calendar/types'

// ==========================================
// GET /api/v1/calendars/[calendarId]/settings
// Get calendar settings including working hours and date overrides
// ==========================================
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin

    // Get calendar with all settings
    const { data: calendar, error } = await supabase
      .from('calendars')
      .select(`
        *,
        calendar_working_hours (*),
        calendar_date_overrides (*)
      `)
      .eq('id', calendarId)
      .single()

    if (error || !calendar) {
      return NextResponse.json(
        { success: false, error: 'Calendar not found' },
        { status: 404 }
      )
    }

    // Transform data
    const settings = {
      calendar: {
        id: calendar.id,
        name: calendar.name,
        slug: calendar.slug,
        description: calendar.description,
        calendarType: calendar.calendar_type,
        status: calendar.status,
        timezone: calendar.timezone,
        color: calendar.color,
        avatarUrl: calendar.avatar_url,
        customBranding: calendar.custom_branding
      },
      bookingSettings: {
        durationMinutes: calendar.duration_minutes,
        slotIntervalMinutes: calendar.slot_interval_minutes,
        bufferBeforeMinutes: calendar.buffer_before_minutes,
        bufferAfterMinutes: calendar.buffer_after_minutes,
        minimumNoticeHours: calendar.minimum_notice_hours,
        maximumBookingDays: calendar.maximum_booking_days,
        autoConfirm: calendar.auto_confirm,
        requiresApproval: calendar.requires_approval,
        maxCapacity: calendar.max_capacity,
        minAttendees: calendar.min_attendees,
        waitlistEnabled: calendar.waitlist_enabled,
        allowGuests: calendar.allow_guests ?? false,
        maxGuests: calendar.max_guests ?? 5
      },
      reschedulingSettings: {
        allowReschedule: calendar.allow_reschedule,
        rescheduleDeadlineHours: calendar.reschedule_deadline_hours,
        maxReschedules: calendar.max_reschedules,
        allowCancellation: calendar.allow_cancellation,
        cancellationDeadlineHours: calendar.cancellation_deadline_hours
      },
      workingHours: (calendar.calendar_working_hours || []).map((wh: any) => ({
        id: wh.id,
        calendarId: wh.calendar_id,
        dayOfWeek: wh.day_of_week,
        isAvailable: wh.is_available,
        timeBlocks: wh.time_blocks,
        createdAt: wh.created_at,
        updatedAt: wh.updated_at
      })).sort((a: any, b: any) => a.dayOfWeek - b.dayOfWeek),
      dateOverrides: (calendar.calendar_date_overrides || []).map((d: any) => ({
        id: d.id,
        calendarId: d.calendar_id,
        overrideDate: d.override_date,
        overrideType: d.override_type,
        reason: d.reason,
        customHours: d.custom_hours,
        isRecurring: d.is_recurring,
        recurrencePattern: d.recurrence_pattern,
        createdAt: d.created_at
      })).sort((a: any, b: any) => new Date(a.overrideDate).getTime() - new Date(b.overrideDate).getTime())
    }

    return NextResponse.json({
      success: true,
      data: settings
    })

  } catch (error) {
    console.error('Error in GET /api/v1/calendars/[calendarId]/settings:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// PUT /api/v1/calendars/[calendarId]/settings
// Update calendar settings
// ==========================================
export async function PUT(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      // Basic settings
      name,
      description,
      timezone,
      color,
      avatarUrl,
      customBranding,
      // Booking settings
      durationMinutes,
      slotIntervalMinutes,
      bufferBeforeMinutes,
      bufferAfterMinutes,
      minimumNoticeHours,
      maximumBookingDays,
      autoConfirm,
      requiresApproval,
      maxCapacity,
      minAttendees,
      waitlistEnabled,
      allowGuests,
      maxGuests,
      // Rescheduling settings
      allowReschedule,
      rescheduleDeadlineHours,
      maxReschedules,
      allowCancellation,
      cancellationDeadlineHours,
      // Working hours
      workingHours,
      // Date overrides
      dateOverrides
    } = body

    // Build calendar updates
    const calendarUpdates: Record<string, any> = {
      updated_at: new Date().toISOString()
    }

    if (name !== undefined) calendarUpdates.name = name
    if (description !== undefined) calendarUpdates.description = description
    if (timezone !== undefined) calendarUpdates.timezone = timezone
    if (color !== undefined) calendarUpdates.color = color
    if (avatarUrl !== undefined) calendarUpdates.avatar_url = avatarUrl
    if (customBranding !== undefined) calendarUpdates.custom_branding = customBranding
    if (durationMinutes !== undefined) calendarUpdates.duration_minutes = durationMinutes
    if (slotIntervalMinutes !== undefined) calendarUpdates.slot_interval_minutes = slotIntervalMinutes
    if (bufferBeforeMinutes !== undefined) calendarUpdates.buffer_before_minutes = bufferBeforeMinutes
    if (bufferAfterMinutes !== undefined) calendarUpdates.buffer_after_minutes = bufferAfterMinutes
    if (minimumNoticeHours !== undefined) calendarUpdates.minimum_notice_hours = minimumNoticeHours
    if (maximumBookingDays !== undefined) calendarUpdates.maximum_booking_days = maximumBookingDays
    if (autoConfirm !== undefined) calendarUpdates.auto_confirm = autoConfirm
    if (requiresApproval !== undefined) calendarUpdates.requires_approval = requiresApproval
    if (maxCapacity !== undefined) calendarUpdates.max_capacity = maxCapacity
    if (minAttendees !== undefined) calendarUpdates.min_attendees = minAttendees
    if (waitlistEnabled !== undefined) calendarUpdates.waitlist_enabled = waitlistEnabled
    if (allowGuests !== undefined) calendarUpdates.allow_guests = allowGuests
    if (maxGuests !== undefined) calendarUpdates.max_guests = maxGuests
    if (allowReschedule !== undefined) calendarUpdates.allow_reschedule = allowReschedule
    if (rescheduleDeadlineHours !== undefined) calendarUpdates.reschedule_deadline_hours = rescheduleDeadlineHours
    if (maxReschedules !== undefined) calendarUpdates.max_reschedules = maxReschedules
    if (allowCancellation !== undefined) calendarUpdates.allow_cancellation = allowCancellation
    if (cancellationDeadlineHours !== undefined) calendarUpdates.cancellation_deadline_hours = cancellationDeadlineHours

    // Update calendar
    const { error: calError } = await supabase
      .from('calendars')
      .update(calendarUpdates)
      .eq('id', calendarId)

    if (calError) {
      console.error('Error updating calendar:', calError)
      return NextResponse.json(
        { success: false, error: 'Failed to update calendar settings' },
        { status: 500 }
      )
    }

    // Update working hours if provided
    if (workingHours && Array.isArray(workingHours)) {
      // Delete existing and insert new
      await supabase
        .from('calendar_working_hours')
        .delete()
        .eq('calendar_id', calendarId)

      const workingHoursToInsert = workingHours.map((wh: any) => ({
        id: wh.id || uuidv4(),
        calendar_id: calendarId,
        day_of_week: wh.dayOfWeek,
        is_available: wh.isAvailable,
        time_blocks: wh.timeBlocks || []
      }))

      const { error: whError } = await supabase
        .from('calendar_working_hours')
        .insert(workingHoursToInsert)

      if (whError) {
        console.error('Error updating working hours:', whError)
      }
    }

    // Update date overrides if provided
    if (dateOverrides && Array.isArray(dateOverrides)) {
      // Delete existing and insert new
      await supabase
        .from('calendar_date_overrides')
        .delete()
        .eq('calendar_id', calendarId)

      if (dateOverrides.length > 0) {
        const overridesToInsert = dateOverrides.map((d: any) => ({
          id: d.id || uuidv4(),
          calendar_id: calendarId,
          override_date: d.overrideDate,
          override_type: d.overrideType,
          reason: d.reason,
          custom_hours: d.customHours,
          is_recurring: d.isRecurring || false,
          recurrence_pattern: d.recurrencePattern
        }))

        const { error: doError } = await supabase
          .from('calendar_date_overrides')
          .insert(overridesToInsert)

        if (doError) {
          console.error('Error updating date overrides:', doError)
        }
      }
    }

    return NextResponse.json({
      success: true,
      message: 'Calendar settings updated successfully'
    })

  } catch (error) {
    console.error('Error in PUT /api/v1/calendars/[calendarId]/settings:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// POST /api/v1/calendars/[calendarId]/settings/working-hours
// Add/update a single working hours entry
// ==========================================
export async function POST(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin
    const body = await request.json()
    const { searchParams } = new URL(request.url)
    const action = searchParams.get('action')

    if (action === 'date-override') {
      // Add date override
      const { overrideDate, overrideType, reason, customHours, isRecurring, recurrencePattern } = body

      if (!overrideDate || !overrideType) {
        return NextResponse.json(
          { success: false, error: 'overrideDate and overrideType are required' },
          { status: 400 }
        )
      }

      const validTypes: DateOverrideType[] = ['blocked', 'available', 'holiday']
      if (!validTypes.includes(overrideType)) {
        return NextResponse.json(
          { success: false, error: `Invalid overrideType. Must be one of: ${validTypes.join(', ')}` },
          { status: 400 }
        )
      }

      // Check for existing override on same date
      const { data: existing } = await supabase
        .from('calendar_date_overrides')
        .select('id')
        .eq('calendar_id', calendarId)
        .eq('override_date', overrideDate)
        .single()

      if (existing) {
        // Update existing
        const { error } = await supabase
          .from('calendar_date_overrides')
          .update({
            override_type: overrideType,
            reason,
            custom_hours: customHours,
            is_recurring: isRecurring || false,
            recurrence_pattern: recurrencePattern
          })
          .eq('id', existing.id)

        if (error) {
          return NextResponse.json(
            { success: false, error: 'Failed to update date override' },
            { status: 500 }
          )
        }

        return NextResponse.json({
          success: true,
          data: { id: existing.id },
          message: 'Date override updated'
        })
      }

      // Insert new
      const overrideId = uuidv4()
      const { error } = await supabase
        .from('calendar_date_overrides')
        .insert({
          id: overrideId,
          calendar_id: calendarId,
          override_date: overrideDate,
          override_type: overrideType,
          reason,
          custom_hours: customHours,
          is_recurring: isRecurring || false,
          recurrence_pattern: recurrencePattern
        })

      if (error) {
        console.error('Error creating date override:', error)
        return NextResponse.json(
          { success: false, error: 'Failed to create date override' },
          { status: 500 }
        )
      }

      return NextResponse.json({
        success: true,
        data: { id: overrideId },
        message: 'Date override created'
      }, { status: 201 })
    }

    // Default: Update working hours for a specific day
    const { dayOfWeek, isAvailable, timeBlocks } = body

    if (dayOfWeek === undefined || dayOfWeek < 0 || dayOfWeek > 6) {
      return NextResponse.json(
        { success: false, error: 'dayOfWeek must be a number between 0 (Sunday) and 6 (Saturday)' },
        { status: 400 }
      )
    }

    // Upsert working hours for the day
    const { data: existing } = await supabase
      .from('calendar_working_hours')
      .select('id')
      .eq('calendar_id', calendarId)
      .eq('day_of_week', dayOfWeek)
      .single()

    if (existing) {
      // Update existing
      const { error } = await supabase
        .from('calendar_working_hours')
        .update({
          is_available: isAvailable !== undefined ? isAvailable : true,
          time_blocks: timeBlocks || [],
          updated_at: new Date().toISOString()
        })
        .eq('id', existing.id)

      if (error) {
        return NextResponse.json(
          { success: false, error: 'Failed to update working hours' },
          { status: 500 }
        )
      }

      return NextResponse.json({
        success: true,
        data: { id: existing.id, dayOfWeek },
        message: 'Working hours updated'
      })
    }

    // Insert new
    const whId = uuidv4()
    const { error } = await supabase
      .from('calendar_working_hours')
      .insert({
        id: whId,
        calendar_id: calendarId,
        day_of_week: dayOfWeek,
        is_available: isAvailable !== undefined ? isAvailable : true,
        time_blocks: timeBlocks || []
      })

    if (error) {
      console.error('Error creating working hours:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to create working hours' },
        { status: 500 }
      )
    }

    return NextResponse.json({
      success: true,
      data: { id: whId, dayOfWeek },
      message: 'Working hours created'
    }, { status: 201 })

  } catch (error) {
    console.error('Error in POST /api/v1/calendars/[calendarId]/settings:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// DELETE /api/v1/calendars/[calendarId]/settings
// Delete date override
// ==========================================
export async function DELETE(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const overrideId = searchParams.get('overrideId')
    const overrideDate = searchParams.get('overrideDate')

    if (!overrideId && !overrideDate) {
      return NextResponse.json(
        { success: false, error: 'overrideId or overrideDate is required' },
        { status: 400 }
      )
    }

    let query = supabase
      .from('calendar_date_overrides')
      .delete()
      .eq('calendar_id', calendarId)

    if (overrideId) {
      query = query.eq('id', overrideId)
    } else if (overrideDate) {
      query = query.eq('override_date', overrideDate)
    }

    const { error } = await query

    if (error) {
      console.error('Error deleting date override:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to delete date override' },
        { status: 500 }
      )
    }

    return NextResponse.json({
      success: true,
      message: 'Date override deleted'
    })

  } catch (error) {
    console.error('Error in DELETE /api/v1/calendars/[calendarId]/settings:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}
